body {
  background-color: rgb(14, 13, 13);
  color: white;
}

html,
body {
  overflow-x: hidden;
}

.home-page {
  color: teal !important;
}

.btn:hover,
a:hover {
  color: rgb(14, 165, 165) !important;
}

.btn {
  color: teal;
}

#button-teal {
  color: white;
  background-color: teal;
}

#button-teal:hover {
  color: white !important;
  background-color: rgb(14, 165, 165);
}

a {
  text-decoration: none;
  color: teal;
}

.my-image {
  border-radius: 10px;
}

.active-page-link {
  color: teal !important;
}

.shadow-separation {
  filter: drop-shadow(0px 0 3px rgba(225, 255, 74, 0.575)) !important;
}

.devLink {
  color: teal;
  cursor: pointer;
}

@media (orientation: landscape) and (min-width: 992px) {
  .float-to-right {
    display: flex;
    justify-content: flex-end;
  }
}

@media (orientation: portrait) {
  .my-image {
    margin-bottom: 20px;
  }
}
